@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  margin: 0;
  font-family: 'Lato', sans-serif;
}

.main {
  min-height: 100vh;
  padding-right: 415px;
}

@media only screen and (max-width: 1800px) {
  .main {
    padding-right: 0;
  }
}

@media only screen and (max-width: 768px) {
  .content {
    margin-left: 0;
    box-sizing: content-box;
  }
}

.main__wrapper {
  padding: 0 40px 100px;
  max-width: 1430px;
  margin: 0 auto;
}

@media only screen and (max-width: 768px) {
  .main__wrapper {
    max-width: 520px;
  }
}

.aside {
  width: 415px;
  background: #f6f6f6;
  display: flex;
  margin-left: auto;
  min-height: 100%;
  right: 0;
  top: 0;
  position: fixed;
}

@media only screen and (max-width: 1800px) {
  .aside {
    display: none;
  }
}

.header {
  margin-top: 20px;
}

.topline {
  display: flex;
  margin-bottom: 25px;
}

@media only screen and (max-width: 1400px) {
  .topline {
    flex-direction: column;
  }
}

.topline__nav {
  display: inline-block;
}

@media only screen and (max-width: 1024px) {
  .topline__nav {
    display: none;
  }
}

.topline__link {
  display: inline-block;
  font-size: 14px;
  font-weight: 300;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  padding: 6px 17px 6px 21px;
  letter-spacing: 1px;
  border-radius: 4px;
  transition: background .5s;
  margin-right: 6px;
}

.topline__link:last-child {
  margin-right: 0;
}

.topline__link:hover {
  background: #b6b6b6;
}

.topline__link.is__active {
  background: #d8d8d8;
}

::-webkit-input-placeholder {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
}

::-moz-placeholder {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
}

:-moz-placeholder {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
}

:-ms-input-placeholder {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
}

.header__right {
  margin-left: auto;
  display: inline-block;
}

@media only screen and (max-width: 1400px) {
  .header__right {
    margin-left: 0;
    margin-top: 20px;
  }
}

.header__search {
  margin-right: 37px;
  width: 323px;
}

.search {
  position: relative;
  margin-right: 35px;
  display: inline-block;
  max-width: 323px;
}

@media only screen and (max-width: 1024px) {
  .search {
    display: none;
  }
}

@media only screen and (max-width: 768px) {
  .search {
    margin-bottom: 20px;
  }
}

.search__input {
  width: 100%;
  padding: 0 28px 0 11px;
  line-height: 24px;
  border-radius: 4px;
  border: 1px solid #d8d8d8;
  font-size: 14px;
  box-sizing: border-box;
  outline: none;
}

.search__button {
  position: absolute;
  top: 0;
  right: 0;
  width: 16px;
  height: 16px;
  display: block;
  background: transparent;
  outline: none;
  border: none;
}

.search__button-img {
  position: absolute;
  top: 4px;
  right: 2px;
}

.language,
.search__form {
  display: inline-block;
}

.language .topline__link {
  margin-right: 0;
  padding: 7px 8px 7px;
}

.header__bottom {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 35px;
}

.header__nav {
  display: inline-block;
  margin-left: auto;
}

@media only screen and (max-width: 1024px) {
  .header__nav {
    display: none;
  }
}

.nav__link {
  font-size: 20px;
  font-weight: 300;
  color: #004f9e;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: 0.5s;
  margin-right: 37px;
  line-height: 1.5;
}

.nav__link:last-child {
  margin-right: 0;
}

.nav__link:hover, .nav__link.is-active {
  color: #000000;
  border-bottom: 1px solid #f18b00;
}

.accordion {
  margin-top: 41px;
  margin-left: -12px;
}

.accordion__content {
  max-width: 1402px;
  padding: 10px 5px 15px 30px;
  display: none;
  border-left: 1px solid #d4d4d4;
  border-right: 1px solid #d4d4d4;
  font-size: 18px;
  font-weight: 300;
  line-height: 30px;
  transition: 0.5s;
}

@media only screen and (max-width: 768px) {
  .accordion__content {
    font-size: 12px;
  }
}

.accordion__title,
.accordion__icon {
  display: inline-block;
}

.accordion__icon {
  margin-left: auto;
  transition: 0.5s;
}

.accordion__icon.active {
  display: none;
  transition: 0.5s;
}

.accordion__icon-line {
  width: 21px;
  height: 3px;
  background-color: #004f9e;
  display: inline-block;
  position: absolute;
}

@media only screen and (max-width: 768px) {
  .accordion__icon-line {
    width: 15px;
    height: 2px;
  }
}

.accordion__icon-vertical {
  transform: rotate(90deg);
}

.accordion__link-small {
  color: #004f9e;
  text-decoration: none;
  transition: 0.5s;
}

.accordion__link-small:hover {
  color: #002d7c;
  text-decoration: none;
}

.link-more {
  color: #004f9e;
  text-decoration: none;
  transition: 0.5s;
}

.link-more span {
  color: #f18b00;
}

.link-more:hover {
  color: #002d7c;
  text-decoration: none;
}

.useful {
  margin-top: 50px;
  margin-bottom: 50px;
}

.useful__title {
  color: #000000;
  font-size: 20px;
  font-weight: 500;
}

.useful__link {
  display: block;
  color: #004f9e;
  font-size: 16px;
  font-weight: 300;
  text-decoration: none;
}

@media only screen and (max-width: 768px) {
  .useful__link {
    font-size: 12px;
  }
}

.useful__link:hover {
  text-decoration: underline;
  color: #002d7c;
}

.accordion__link {
  position: relative;
  font-size: 24px;
  cursor: pointer;
  text-decoration: none;
  max-width: 1340px;
  padding: 25px 30px;
  border-left: 1px solid #d4d4d4;
  border-right: 1px solid #d4d4d4;
  border-top: 1px solid #d4d4d4;
  border-bottom: 1px solid transparent;
  transition: 0.5s;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  .accordion__link {
    font-size: 16px;
    padding: 25px 30px 25px 20px;
  }
}

.accordion__link:hover {
  background-color: #f6f6f6;
  border-bottom: 1px solid #d4d4d4;
}

.accordion__container:last-child .accordion__link {
  border-bottom: 1px solid #d4d4d4;
}

.accordion__container:last-child.accordion__content {
  border-bottom: 1px solid #d4d4d4;
}

.accordion__container:last-child .accordion__link.accordion__link-is__active {
  border-bottom: 1px solid transparent;
}

.accordion__link-no__active {
  background-color: #FFF;
  border-radius: 4px;
  color: #004f9e;
}

.accordion__link-is__active {
  color: black;
  background-color: #FFF;
}

.breadcrumbs__link {
  color: #000000;
  opacity: 0.6;
  font-size: 16px;
  font-weight: 300;
  line-height: 40px;
  text-decoration: none;
  position: relative;
  margin-right: 20px;
}

.breadcrumbs__link:after {
  display: inline-block;
  content: "—";
  width: 16px;
  height: 1px;
  position: absolute;
  margin-left: 4px;
}

.button {
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  line-height: 50px;
  padding: 15px 40px 15px 28px;
  background: #f18b00;
  border-radius: 4px;
  transition: 0.5s;
  position: relative;
  padding-right: 30px;
}

.button:hover {
  background: #cf6900;
}

.button__arrow {
  display: inline-block;
  color: #fff;
  width: 7px;
  height: 16px;
  font-size: 25px;
  position: absolute;
  top: 13px;
  right: 14px;
}

.footer {
  margin-top: 42px;
  margin-bottom: 170px;
}

@media only screen and (max-width: 520px) {
  .footer {
    margin-bottom: 70px;
  }
}

.footer__text {
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  color: #000000;
}

.footer__text a {
  color: #004f9e;
  text-decoration: none;
  transition: 0.5s;
}

.footer__text a:hover {
  color: #002d7c;
  text-decoration: underline;
  cursor: pointer;
}

.content {
  margin-top: 65px;
  margin-left: 37px;
}

.h2 {
  color: #000000;
  font-size: 48px;
  font-weight: 600;
  line-height: 40px;
}

@media only screen and (max-width: 768px) {
  .h2 {
    font-size: 36px;
  }
}

.article {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  max-width: 1200px;
  color: #000000;
  margin-top: 57px;
}

.article p {
  font-size: 20px;
  font-weight: 400;
  line-height: 32px;
}

@media only screen and (max-width: 768px) {
  .article p {
    font-size: 18px;
  }
}

.article img {
  margin-left: 30px;
}

@media only screen and (max-width: 768px) {
  .article img {
    display: none;
  }
}

.h3,
h3 {
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
}

.article h3 {
  margin-top: 46px;
  letter-spacing: 0.75px;
}

.article ul {
  list-style: none;
  color: #000000;
  font-size: 18px;
  font-weight: 300;
  padding-left: 30px;
  margin-top: 5px;
}

@media only screen and (max-width: 520px) {
  .article ul {
    padding-left: 0;
  }
}

.article ul li {
  line-height: 50px;
  position: relative;
  padding-left: 20px;
}

.article ul li:before {
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #f18b00;
  position: absolute;
  top: 21px;
  left: 0;
}

.services {
  margin-top: 94px;
  display: flex;
}

@media only screen and (max-width: 768px) {
  .services {
    flex-direction: column;
    margin-top: 70px;
  }
}

.item {
  width: 29%;
  margin-right: 4%;
  display: inline-block;
}

@media only screen and (max-width: 768px) {
  .item {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 40px;
  }
}

.item-link {
  color: #004f9e;
  font-size: 24px;
  font-weight: 400;
  line-height: 34px;
  text-decoration: none;
}

.item-text {
  color: #000000;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  min-height: 7.8em;
  margin-top: 9px;
  margin-bottom: 20px;
}

@media only screen and (max-width: 768px) {
  .item-text {
    min-height: auto;
  }
}

.hamburger {
  display: none;
  transition: all 0.25s;
  position: absolute;
  right: 3%;
  height: 36px;
  width: 36px;
  z-index: 1;
  cursor: pointer;
  top: 10px;
}

.hamburger ul {
  transition: all 0.25s;
  position: fixed;
  left: -9000px;
}

@media only screen and (max-width: 1024px) {
  .hamburger {
    display: block;
  }
}

.hamburger__nav {
  display: none;
}

.hamburger__nav .nav__link {
  display: block;
  margin-left: 20px;
  font-size: 20px;
  font-weight: 300;
  color: #004f9e;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: 0.5s;
  margin-right: 37px;
  line-height: 1.5;
}

.hamburger__menu-list {
  text-align: center;
}

.hamburger__main-link {
  font-weight: 700;
}

.hamburger-icon {
  position: absolute;
  right: 12px;
  top: 46px;
  transition: all 0.25s;
  content: "";
  height: 1.42px;
  width: 23.3px;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.95);
  position: absolute;
  right: 0;
}

.hamburger-icon:before, .hamburger-icon:after {
  transition: all 0.25s;
  content: "";
  height: 1.42px;
  width: 23.3px;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.95);
  position: absolute;
  right: 0;
}

.hamburger-icon:before {
  top: -8px;
}

.hamburger-icon:after {
  top: 8px;
}

.hamburger-open {
  transition: all 0.25s;
  text-align: center;
  height: 100%;
  width: 100%;
  border-radius: 0px;
  top: 0;
  right: 0;
  background: rgba(90, 104, 114, 0.8);
  opacity: 1.00;
  position: fixed;
  overflow: auto;
}

.hamburger-open ul {
  transition: all 0s;
  position: relative;
  left: 0;
  z-index: 2;
  top: 65px;
  padding-left: 20px;
}

.hamburger-open ul li {
  text-align: left;
  padding: 10px 20px 10px 40px;
  position: relative;
}

.hamburger-open a,
.hamburger-open .nav__link {
  transition: all 0.15s;
  text-decoration: none;
  font-size: 14px;
  padding: 5px;
  color: #fff;
  display: inline-block;
  height: auto;
  cursor: pointer;
}

.hamburger-open a:hover,
.hamburger-open .nav__link:hover {
  transition: all 0.15s;
}

.hamburger-open .nav__link {
  display: block;
}

.hamburger-open .hamburger-icon {
  background-color: transparent;
  transition: all 0.25s;
  top: 48px;
  right: 5px;
}

.hamburger-open .hamburger-icon:before {
  transform: rotate(45deg);
  top: 0;
  right: 5px;
}

.hamburger-open .hamburger-icon:after {
  transform: rotate(-45deg);
  top: 0;
  right: 5px;
}

.wrapper__blur-open {
  filter: blur(10px);
}

.hamburger__nav.open {
  display: block;
}
